export default {
	data() {
		return {
			LabelEntity:{
				LocationCode:'',
				SplitNo:'',
				ReelId:'',
			},
			locationFoucs:true,
			labelFoucs:false,
			SplitNo:''
		}
	},
	onLoad:function(e){
		this.SplitNo=e.SplitNo
		this.LabelEntity.SplitNo=e.SplitNo
	},
	methods: {
		OnEnter(){
			if(this.LabelEntity.LocationCode!='' || this.LabelEntity.ReelId!=''){
				uni.request({
					url: this.apiUrl + 'Split/GetReelIdByLocationCodeForSplit',
					method: "get",
					data:{SplitNo:this.LabelEntity.SplitNo,ReelId:this.LabelEntity.ReelId,LocationCode:this.LabelEntity.LocationCode},
					success: res => {
						var resData = JSON.parse(res.data.Content)
						if (resData.Success) {
							console.log(resData)
							this.LabelEntity =resData.Data
						} else {
							uni.showModal({
								content: resData.Message,
								showCancel: false
							});
							return
						}
				
					},
					fail: () => {
				
					},
					complete: () => {
				
					}
				})
								
			}
		},
		Back(){
			uni.navigateBack({
				
			});
		},
		ConfirmCancel(){
			uni.request({
				url: this.apiUrl + 'Split/CancelSplitReel',
				method: "post",
				data: this.LabelEntity,
				success: res => {
					var resData = JSON.parse(res.data.Content)
					if (resData.Success) {
		                uni.showModal({
							content: resData.Message,
							showCancel: false
						});
						this.LabelEntity={
							SplitNo:this.SplitNo,
							LocationCode:'',
							ReeId:''
						}
					} else {
						uni.showModal({
							content: resData.Message,
							showCancel: false
						});
						return
					}
			
				},
				fail: () => {
			
				},
				complete: () => {
			
				}
			})
				
		}
		
	}
}
